import React, { useState, useEffect } from 'react';
import './Historial.css';
import Spinner from './Spinner'; // Importa el componente Spinner
import HistorialItem from './HistorialItem';
import Modal from './Modal'; // Asegúrate de que la ruta sea correcta

import imagen1 from '../imagenes/enfermera.jpg';
import imagen2 from '../imagenes/dibujo ojito.jpg';
import imagen3 from '../imagenes/Almita.jpg';
import imagen4 from '../imagenes/o.jpg';

const images = [
  { src: imagen1, name: 'Enfermera', date: '2024-08-15' },
  { src: imagen2, name: 'Dibujo Ojito', date: '2024-08-16' },
  { src: imagen3, name: 'Almita', date: '2024-08-17' },
  { src: imagen4, name: 'O', date: '2024-08-18' },
];

const Historial = () => {
  const [selectedImage, setSelectedImage] = useState(null);
  const [isLoading, setIsLoading] = useState(true); // Estado de carga

  const handleOpenModal = (imageUrl) => {
    setSelectedImage(imageUrl);
  };

  const handleCloseModal = () => {
    setSelectedImage(null);
  };

  useEffect(() => {
    // Simula una verificación de conexión o carga de datos
    const timer = setTimeout(() => {
      setIsLoading(false); // Desactiva el estado de carga después de un tiempo
    }, 2000); // Simula un tiempo de carga

    return () => clearTimeout(timer);
  }, []);

  return (
    <div className="historial-container">
      {isLoading ? (
        <Spinner /> // Muestra el spinner si está cargando
      ) : (
        images.map((image, index) => (
          <HistorialItem
            key={index}
            src={image.src}
            name={image.name}
            date={image.date}
            onClick={handleOpenModal}
          />
        ))
      )}
      {selectedImage && (
        <Modal imageUrl={selectedImage} onClose={handleCloseModal} />
      )}
    </div>
  );
};

export default Historial;
