import React, { useState } from 'react';
import './Historial.css'; // Asegúrate de que los estilos están bien importados

const HistorialItem = ({ src, name, date, onClick }) => {
  const [isActive, setIsActive] = useState(false);

  const handleClick = () => {
    setIsActive(!isActive);
    onClick(src); // Llama a la función onClick pasada como prop
  };

  return (
    <div
      className={`historial-item ${isActive ? 'active' : ''}`}
      onClick={handleClick}
    >
      <img src={src} alt={name} loading="lazy" /> {/* Agregado loading="lazy" */}
      <div className="historial-item-info">
        <div className="historial-item-description">
          <div className="historial-item-name">{name}</div>
          <div className="historial-item-date">{date}</div>
        </div>
      </div>
    </div>
  );
};

export default HistorialItem;
